const imagesList = document.querySelector('.card-list');
const loader = document.querySelector('.loader');

function IMGelement(
  comments,
  likes,
  views,
  downloads,
  largeImageURL,
  webformatURL,
  tags
) {
  return `
        <li class="card">
          <div class="img-container">
            <a href="${largeImageURL}">
              <img class="card-image" src="${webformatURL}" alt="${tags}" />
            </a>
          </div>
          <ul class="card-stats">
            <li><b>Likes</b><span>${likes}</span></li>
            <li><b>Views</b><span>${views}</span></li>
            <li><b>Comments</b><span>${comments}</span></li>
            <li><b>Downloads</b><span>${downloads}</span></li>
          </ul>
        </li>
    `;
}

function IMGelements(imgs) {
  return imgs
    .map(elem => {
      const {
        comments,
        likes,
        views,
        downloads,
        largeImageURL,
        webformatURL,
        tags,
      } = elem;
      return IMGelement(
        comments,
        likes,
        views,
        downloads,
        largeImageURL,
        webformatURL,
        tags
      );
      // console.log(comments, likes, views, downloads, previewURL, tags);
    })
    .join('\n');
}

function clearGallery() {
  imagesList.innerHTML = '';
}

export function showLoader() {
  loader.classList.add('is-visible');
}
export function hideLoader() {
  loader.classList.remove('is-visible');
}

export function createGallery(imgArr) {
  clearGallery();
  imagesList.innerHTML = IMGelements(imgArr);
}
